.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.6rem;
    font-weight: 700;
    font-family: var(--font-family);
    min-width: 100px;
    padding: 6px 16px;
    border-radius: 3px;
    cursor: pointer;
    user-select: none;
    background-color: var(--white-color);

    + .wrapper {
        margin-left: 16px;
    }

    &.disabled {
        cursor: default;
        opacity: 0.5;
    }
}

.icon {
    display: block;
    text-align: center;
    font-size: 18px;
    width: 23px;
}

.icon + .title,
.title + .icon {
    margin-left: 8px;
}

.rounded {
    border: 1px solid transparent;

    background-color: var(--white-color);
    border-radius: 99px;
    box-shadow: 0 0 4px rgba(22, 24, 35, 0.2);

    &:hover {
        border-color: rgba(22, 24, 35, 0.2);
    }
}

.primary {
    color: var(--white-color);
    background-color: var(--primary-color);
    padding: 6px 8px;

    &:hover {
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)), rgb(254, 44, 85);
    }
}

.outline {
    color: var(--primary-color);
    border: 1px solid currentColor;

    &:hover {
        border: 1px solid currentColor;
        background-color: rgba(254, 44, 85, 0.08);
    }
}

.text {
    border: 1px solid rgba(22, 24, 35, 0.12);
    color: rgba(22, 24, 35, 0.8);
    padding: 6px 14px;

    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}

// Button size

/*
   Bug: đặt button class outline và outline không ăn
*/
.small {
    min-width: 88px;
    padding: 4px 16px;
}

.large {
    min-width: 140px;
    padding: 14px 16px;
}
