.wrapper {
   display: flex;
   flex-direction: column;
   position: absolute;
   overflow-y: auto;
   width: 100%;
   
   // 133px là khi chỉ hiển thị account mà không hiển thị các từ khóa 
   max-height: min((100vh - 96px) - 60px, 734px);
   min-height: 100px;
   background: rgb(255, 255, 255);
   box-shadow: rgba(0, 0, 0, 0.12) 0px 2px 12px;
   border-radius: 8px;
}
