.wrapper {
    position: relative;
    display: flex;
    padding: 9px 16px;
    align-items: center;
    height: 58px;
    cursor: pointer;

    &:hover {
        background-color: rgba(22, 24, 35, 0.03);

        .expand {
            display: block;
        }
    }
}

.avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 12px;
}

.info {
    flex: 1;
}

.name {
    font-weight: 500;
    color: #000;
    font-size: 1.6rem;
}

.checkIcon {
    font-size: 1.3rem;
    margin-left: 8px;
    color: rgb(32, 213, 236);
}

.username {
    font-size: 1.4rem;
    color: rgba(22, 24, 35, 0.5);
    line-height: 1;
}

.expand {
    position: absolute;
    
    right: 20px;
    // display: none;
    padding: 10px;
    font-size: 1.5rem;
    z-index: 100;
    &:hover .expandMenu {
        display: block;
        background-color: red;
    }
}

.expandMenu {
    top: 28px;
    right: -329px;
    // display: none;
    position: relative;
    z-index: 100;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.14);
    background-color: #fff;
    min-width: 250px;
    width: 300px;

    &::before {
        position: absolute;
        top: -21px;
        right: 5px;
        content: '';
        border-width: 12px;
        border-color: transparent transparent white transparent;
        border-style: solid;
    }

    &::after {
        position: absolute;
        top: -10px;
        right: 0;
        content: '';
        display: block;
        height: 20px;
        width: 60%;
    }
}

.expandItem {
    padding: 10px;
    display: block;
    border-bottom: 1px solid rgba(22, 24, 35, 0.1);
    font-size: 18px;

    &:hover {
        color: var(--primary-color);
    }

    .expandItemIcon {
        display: inline;

        padding-right: 10px;
    }

    span {
        display: inline;
    }
}
